@use "abstracts" as *;

.socials {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-items: center;
    justify-content: center;
    align-self: center;
    margin: 2vh 0;
    padding: 0 2rem;

    //background: no-repeat 2rem 2rem col(10);
}

.social-link {
    // Draw Circle
    $diameter: 4rem;
    @include circle(col(40), #{$diameter});
    box-shadow: col(20) 0 0 .3rem .2rem;
    opacity: 0.75;

    margin: 0.5rem;
    padding: 0.75rem;
    border: none;

    &:hover, &:focus {
        @include circle(col(50), #{$diameter});
        outline: none;
        box-shadow: col(20) 0 0 .5rem .5rem;
        fill: white;
    }

    * {
        background-color: col(10);
    }
}